import { NextRequest, NextResponse } from "next/server"
import { wasteLogs, pickupRequests, users } from "@/app/api/_data"
import type { WasteLog } from "@/app/api/_types"

type AvailableItem = {
  id: string
  cafeId: string
  cafeName: string
  itemName: string
  type: string
  quantity: number
  expiryDate: string
  status: string
  description?: string
}

// GET /api/charity/available
// Lists edible waste items assigned to charity that are still pending and available for donation.
// Only shows items that:
// - Are assigned to "charity"
// - Have status "pending"
// - Are not already requested (no pickup request exists for this waste)
// Public endpoint - no authentication required
export async function GET(req: NextRequest) {
  // Allow public access - no authentication required

  // Get all waste IDs that already have pickup requests
  const requestedWasteIds = new Set(
    pickupRequests.map((p) => p.wasteId)
  )

  const now = new Date()

  const items: AvailableItem[] = wasteLogs
    .filter((w) => {
      const isEdible = w.type === "edible"
      const isPending = w.status === "pending"
      const assignedToCharity = w.assignedTo === "charity"
      const notRequested = !requestedWasteIds.has(w.id)

      // Filter out expired items
      let notExpired = true
      if (w.expiryDate) {
        const expiry = new Date(w.expiryDate)
        notExpired = !isNaN(expiry.getTime()) && expiry >= now
      }

      return isEdible && isPending && assignedToCharity && notRequested && notExpired
    })
    .map((w) => {
      // Find cafe name from users
      const cafeUser = users.find((u) => u.cafeId === w.cafeId)
      return {
        id: w.id,
        cafeId: w.cafeId,
        cafeName: cafeUser?.organizationName || "Green Café",
        itemName: w.itemName,
        type: w.type,
        quantity: w.quantity,
        expiryDate: w.expiryDate,
        status: w.status,
        description: w.notes,
      }
    })

  return NextResponse.json(items)
}

